@import "https://fonts.googleapis.com/css?family=Droid+Sans:400,700";
* {
  margin: 0;
  padding: 0;
}

.egor {
  width: 100%;
  height: 100%;
  color: #ff6;
  background-image: url("../cosmos.ce34f980.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Droid Sans, arial, verdana, sans-serif;
  font-weight: 700;
  overflow: hidden;
}

p#start {
  text-align: center;
  width: 16em;
  color: #4ee;
  opacity: 0;
  z-index: 1;
  -ms-animation: intro 2s ease-out;
  margin: 20% auto;
  font-family: Droid Sans, arial, verdana, sans-serif;
  font-size: 200%;
  font-weight: 400;
  animation: intro 2s ease-out;
}

@keyframes intro {
  0% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.egor-title {
  width: 2.6em;
  letter-spacing: -.05em;
  color: #000;
  text-shadow: -2px -2px #ff6, 2px -2px #ff6, -2px 2px #ff6, 2px 2px #ff6;
  opacity: 0;
  z-index: 1;
  -ms-animation: logo 5s ease-out 2.5s;
  font-size: 10em;
  line-height: .8em;
  animation: logo 5s ease-out 2.5s;
  position: absolute;
  top: 200px;
  left: 679px;
}

.red {
  color: #d41443;
}

.egor-title sub {
  letter-spacing: 0;
  font-size: .3em;
  line-height: .8em;
  display: block;
}

@-webkit-keyframes logo {
  0% {
    opacity: 1;
    -webkit-transform: scale(1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    -webkit-transform: scale(.1);
  }
}

@-moz-keyframes logo {
  0% {
    opacity: 1;
    -moz-transform: scale(1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    -moz-transform: scale(.1);
  }
}



@-o-keyframes logo {
  0% {
    opacity: 1;
    -o-transform: scale(1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    -o-transform: scale(.1);
  }
}

@keyframes logo {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: scale(.1);
  }
}

#titles {
  width: 18em;
  height: 50em;
  text-align: justify;
  transform-origin: 50% 100%;
  -webkit-transform: perspective(300px)rotateX(25deg);
  -moz-transform: perspective(300px)rotateX(25deg);
  -ms-transform: perspective(300px)rotateX(25deg);
  -o-transform: perspective(300px)rotateX(25deg);
  margin-left: -9em;
  font-size: 350%;
  position: absolute;
  bottom: 0;
  left: 50%;
  overflow: hidden;
  transform: perspective(300px)rotateX(8deg);
}

#titles:after {
  content: " ";
