  background-image: -ms-linear-gradient(top, #000 0%, transparent 100%);
  background-image: -o-linear-gradient(top, #000 0%, rgba(0, 0, 0, 0) 100%);
  background-image: linear-gradient(top, #000 0%, transparent 100%);
  pointer-events: none;
  background-image: -webkit-linear-gradient(top, #000 0%, rgba(0, 0, 0, 0) 100%);
  background-image: -moz-linear-gradient(top, #000 0%, rgba(0, 0, 0, 0) 100%);
  position: absolute;
  top: 0;
  bottom: 60%;
  left: 0;
  right: 0;
}

.egor-txt {
  text-align: justify;
  margin: .8em 0;
}

#titles .egor-txt .center {
  text-align: center;
}

#titles a {
  color: #ff6;
  text-decoration: underline;
}

#titlecontent {
  -ms-animation: scroll 100s linear 4s infinite;
  animation: scroll 100s linear 4s infinite;
  position: absolute;
  top: 100%;
}

@keyframes scroll {
  0% {
    top: 100%;
  }

  100% {
    top: -170%;
  }
}

